.hero-bg {
  position: relative;
  display: flex;
  background-color: var(--third-bg-color);
  background-image: url(../images/hero/ellipse-nodesktop@1x.png);
  background-repeat: no-repeat;
  background-position: bottom 75px left 56px;
  background-size: 345px auto;
  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/hero/ellipse-nodesktop@2x.png);
  }

  @media screen and (min-width: 768px) {
    background-image: url(../images/hero/sotasty-tablet@1x.png),
      url(../images/hero/milk-tablet@1x.png), url(../images/hero/ellipse-nodesktop@1x.png);
    background-repeat: no-repeat;
    background-position: bottom left 566px, bottom 28px left 34px, bottom 46px left 307px;

    background-size: 168px auto, 124px auto, 324px auto;
    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/hero/sotasty-tablet@2x.png),
        url(../images/hero/milk-tablet@2x.png), url(../images/hero/ellipse-nodesktop@2x.png);
    }
  }

  @media screen and (min-width: 1200px) {
    background-image: url(../images/hero/sotasty-desktop@1x.png),
      url(../images/hero/milk-desktop@1x.png), url(../images/hero/ellipse-desktop@1x.png);
    background-repeat: no-repeat;
    background-position: bottom left 878px, bottom 45px left 77px, bottom 75px left 475px;

    background-size: 293px auto, 188px auto, 538px auto;
    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/hero/sotasty-desktop@2x.png),
        url(../images/hero/milk-desktop@2x.png), url(../images/hero/ellipse-desktop@2x.png);
    }
  }
}
